'use scrict';
import LjlMenuItem from './components/LjlMenuItem.vue';

export default {
	props: {
		infor: {
			type: Object,
			default() {
				return {};
			}
		}
	},
	methods: {
		
		/**
		 * 链接到路由
		 */
		linkToRoute({route,index}) {
			uni.navigateTo({
				url: `${route}?index=${index}`,
				fail() {
					uni.switchTab({ url: route });
				}
			});
		}
	},
	components: {
		LjlMenuItem
	}
}
